<template>
  <div class="content">
    <!-- <SignUp></SignUp>{{}} -->
    <div class="noticeBox am-border" v-for="(item, index) in noticePlateArr" :key="index"
    @click="articleJump(item.bidding_article_id)">
        <div class="left" >
          <!-- <i class="iconfont fire">&#xe629;</i>
          推荐位 -->
          <div>置顶</div>
        </div>
        <div  :class="['van-ellipsis','right',item.bidding_article_id==0?'del':'']">
          {{item.tit| fNoArticle}}
          <!-- <van-notice-bar :text="item.msg" background="#ecf9ff" color="#1989fa"/> -->
        </div>
    </div>

    <div class="noticeBoxBox" v-for="(item, index) in noticePlateArr" :key="100+index"
    v-show='$route.path == "/bidding"'>
        第{{index+1}}名 : <i class="iconfont fire">&#xe601;</i>{{item.bidding_price}}
    </div>


  </div>
</template>

<script>
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"

  import {createNamespacedHelpers} from 'vuex'
  const notice=createNamespacedHelpers('notice')
  const gridVuex=createNamespacedHelpers('grid')

  export default{
    name:"",
    /* 数据*/
    data(){
      return {
        // notices : []
      }
    },
    filters:{
      // fA : function(value){
      //   if(value){
      //     return  value
      //   }else{
      //     /* 如果value是假 */
      //     return '文章飞到其他星球了'
      //   }
      // }
    },
    /* 方法*/
    methods:{
      ...notice.mapMutations({
            	noticePlateArrHandler: 'noticePlateArrHandler'
            }),
      // 跳转到具体文章
      articleJump(params){
        if(params){
          this.$router.push({
            path: '/read',
            query : {
              articleId : params
            }
          })
        }else{
          this.$toast("文章飞到其他星球了")
        }

      },
    },
    /* 计算*/
    computed:{
      ...gridVuex.mapState({
           	grid : state => state.grid
           }),
     ...notice.mapState({
          	noticePlateArr : state => state.noticePlateArr
          })
    },
    // 组件
    components : {

    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
    },
    /* 挂载阶段*/
    mounted() {
      let params ={};
      if(this.$route.query.plate!==undefined ){//在板块页
        params.bidding_type = this.$route.query.plate
      }else
      if(this.$route.query.articleId){//在竞标页
          for(let i = 0; i<this.grid.length;i++){
            if(this.grid[i].text == this.$route.query.article_plate){
              params.bidding_type = i
            }
          }
          params.articleId  = this.$route.query.articleId
      }

      this.$api.article.getNowBiddingPlate(params).then(
          res =>{
            // console.log(this.$route.query.plat,res.data.result,'板块竞标')
            this.noticePlateArrHandler(res.data.result)
          }
      )
    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
  .content{
    width: 640px;
    background-color: white;
    margin-bottom: 10px;
    padding-left: 15px;
  }
  .del{
      color:$jsHomeBottom !important;
    }
  .noticeBox{
   @include flex(row,center,center)
   height: 40px;
   .left,
   .right{

   }
    .left{
      color: $red;
      // width: 150px;
      @include wh(60px,100%);
      @include flex(row,flex-start,center)
      div{
        font-size:14px;
        padding: 4px 10px;
        border-radius: 10px;
        background-color: #EA6F5A;
        color:#FFFFFF;
        font-weight: 300;
      }
    }
    .right{
      font-size:18px;
      font-weight: 600;
      // width: 600px;
      @include wh(580px,100%);
      color:black;
      @include flex(row,flex-start,center);
    }
  }
  .noticeBoxBox{
    font-size: 18px;
    color: $red;
    // background-color: #ecf9ff;
    @include flex(row,flex-start,center)
    .fire{
      font-size:18px
    }
  }
</style>



