<template>
  <div class="biddingBox am-biddingBox">
    <!-- 首页1111111111111111111111111111111111111111111111111111111111111111111111-->
    <Notice></Notice>
    <!-- 竞标 -->
    <div class="curArtBidding_price">
      您当前文章的首页推荐竞标值为:<i class="iconfont fire">&#xe601;</i>{{curArtBidding_price}}
    </div>
    <div class="bidding" @click="bidding(0)">
      <i class="iconfont">&#xe613;</i>
      &nbsp;竞标首页推荐位
    </div>
    <!-- 打赏popup-->
    <van-popup class="showPopupForBiddingModel" v-model="showPopupForBiddingModel" position="top"
    :style="{ height: '240px' }" >
          <div class="tit">土豪,请消费</div>
          <div class="one"> 您的&nbsp;<i class="iconfont gold">&#xe601;</i>&nbsp;数量为:
              <span class="userGold">{{userInfo.user_gold}} </span>
          </div>
          <div style="text-align:center;">
            <input type="text" @input="input" v-model='inputVal'
            placeholder="请输入您打赏的钻石数量">
          </div>

          <div class="pay" @click="confirmReward()" >
            <span v-show="!ws_bidding">我要竞标</span>
            <span v-show="ws_bidding">竞标中...</span>
          </div>
     </van-popup>
    <!-- 板块1111111111111111111111111111111111111111111111111111111111111111111111-->
    <NoticePlate></NoticePlate>
    <div class="curArtBidding_price">
      您当前文章的{{$route.query.article_plate}}板块推荐竞标值为:<i class="iconfont fire">&#xe601;</i>{{curArtBiddingPlate_price}}
    </div>
    <div class="bidding" @click="bidding(1)">
      <i class="iconfont">&#xe613;</i>
      &nbsp;竞标{{$route.query.article_plate}}板块推荐位
    </div>
    <!-- 打赏popup-->
    <van-popup class="showPopupForBiddingModel" v-model="showPopupForPlate" position="top"
    :style="{ height: '240px' }" >
          <div class="tit">土豪,请消费</div>
          <div class="one"> 您的&nbsp;<i class="iconfont gold">&#xe601;</i>&nbsp;数量为:
              <span class="userGold">{{userInfo.user_gold}} </span>
          </div>
          <div style="text-align:center;">
            <input type="text" @input="input" v-model='inputVal'
            placeholder="请输入您打赏的钻石数量">
          </div>

          <div class="pay" @click="confirmPlate()" >
            <span v-show="!ws_biddingPlate">我要竞标</span>
            <span v-show="ws_biddingPlate">竞标中...</span>
          </div>
     </van-popup>

  </div>

</template>

<script>
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"
  // 信息栏
  import Notice from "@/components/utils/Notice"
  import NoticePlate from "@/components/utils/NoticePlate"

  import {createNamespacedHelpers} from 'vuex'
  // const popup=createNamespacedHelpers('popup')
  const notice=createNamespacedHelpers('notice')
  const login=createNamespacedHelpers('login')
  const gridVuex=createNamespacedHelpers('grid')
  export default{
    name:"",
    /* 数据*/
    data(){
      return {
        showPopupForBiddingModel : false,
        inputVal : '',
        ws_bidding : false,
        /* 您当前文章的首页推荐竞标值为*/
        curArtBidding_price : 0,
        /* 您当前文章的某个板块推荐竞标值为*/
        curArtBiddingPlate_price : 0,
        /* 板块*/
        showPopupForPlate : false,
        ws_biddingPlate : false,
      }
    },
    /* 方法*/
    methods:{
      ...login.mapMutations({
            	setHandler: 'setHandler'
            }),

      ...notice.mapMutations({
            	noticeArrHandler: 'noticeArrHandler'
            }),
      ...notice.mapMutations({
            	noticePlateArrHandler: 'noticePlateArrHandler'
            }),
      //展示竞标弹窗
      // ...popup.mapMutations({
      //       	showPopupForBiddingHandler: 'showPopupForBiddingHandler'
      //       }),
      bidding(arg){
        switch(arg){
          case 0:
            this.showPopupForBiddingModel = true
            break;
          case 1:
            this.showPopupForPlate = true
            break;
        }



        // 展示竞标框为true
        // this.showPopupForBiddingHandler(true)
      },
      // input事件
      input(){
        this.inputVal=this.inputVal.replace(/[^\d]/g,'')
        console.log(this.inputVal)
      },
      /* 板块*/
      confirmPlate(){
        //判断是否登录
        if(!!this.hasLogin == false){
          this.$toast("您尚未登录")
          return;
        }
        if(!!this.inputVal == false){
          this.$toast("打赏金额为空")
          return;
        }
        if(this.inputVal > this.userInfo.user_gold){
          this.$toast("超出了你的钻石数量")
          return;
        }
        if(!!this.ws_biddingPlate == true){
          this.$toast("请等待服务器响应")
          return;
        }
        this.ws_biddingPlate = true
        // alert(0)
        /* 板块*/
        var params = {
          num : this.inputVal,
          articleId : this.$route.query.articleId,
          articleUserId :this.$route.query.articleUserId,
        }
        for(let i = 0; i<this.grid.length;i++){
          if(this.grid[i].text == this.$route.query.article_plate){
            params.bidding_type = i
          }
        }
        // 关闭popup
        this.showPopupForBiddingModel = false
        /* 遮罩层*/
        this.isDoing = true
        this.$api.article.biddingPlate(params).then(
            res =>{
              /* 遮罩层*/
              this.isDoing = false
              this.ws_biddingPlate = false
                switch(res.data){
                  case 'err':
                        this.$toast("文章id和用户id匹配不成功!")
                        break;
                  case 'success':
                        break;
                  default:
                  // 更新推荐栏
                  if(res.data.bidding_price){
                    this.curArtBiddingPlate_price = res.data.bidding_price
                  }
                  /* 同步首页*/
                  this.noticePlateArrHandler(res.data.result)
                  // var tempVar  = this.userInfo.user_gold - params.num
                  //设置金币
                  this.setHandler(['user_gold',res.data.user_gold])
                  this.$toast("竞标成功!")
                  // 关闭popup
                  this.showPopupForPlate = false
                  // this.showPopupForBidding = false
                  /* default结束*/
                }
            }
        )
      },
      // 确认竞标
      confirmReward(arg){
        //判断是否登录
        if(!!this.hasLogin == false){
          this.$toast("您尚未登录")
          return;
        }
        // if(this.articleId==0 || this.articleUserId == 0){
        //   this.$toast("请选择文章")
        //   return;
        // }
        if(!!this.inputVal == false){
          this.$toast("打赏金额为空")
          return;
        }
        if(this.inputVal > this.userInfo.user_gold){
          this.$toast("超出了你的钻石数量")
          return;
        }
        if(!!this.ws_bidding == true){
          this.$toast("请等待服务器响应")
          return;
        }
        this.ws_bidding = true
          /* 首页*/
          var params = {
            num : this.inputVal,
            articleId : this.$route.query.articleId,
            articleUserId :this.$route.query.articleUserId,
          }
          // 关闭popup
          this.showPopupForBiddingModel = false
          /* 遮罩层*/
          this.isDoing = true
          this.$api.article.bidding(params).then(
              res =>{
                /* 遮罩层*/
                this.isDoing = false
                this.ws_bidding = false
                  switch(res.data){
                    case 'err':
                          this.$toast("文章id和用户id匹配不成功!")
                          break;
                    case 'success':
                          break;
                    default:
                    // 更新推荐栏
                    if(res.data.bidding_price){
                      this.curArtBidding_price = res.data.bidding_price
                    }
                    /* 同步首页*/
                    this.noticeArrHandler(res.data.result)
                    // var tempVar  = this.userInfo.user_gold - params.num
                    //设置金币
                    this.setHandler(['user_gold',res.data.user_gold])
                    this.$toast("竞标成功!")
                    // // 关闭popup
                    // this.showPopupForBiddingModel = false
                    // this.showPopupForBidding = false
                    /* default结束*/
                  }
              }
          )


        // var params = {
        //   num : this.inputVal,
        //   articleId : this.$route.query.articleId,
        //   articleUserId :this.$route.query.articleUserId,
        // }

        // this.$api.article.bidding(params).then(
        //     res =>{
        //       this.ws_bidding = false
        //         switch(res.data){
        //           case 'err':
        //                 this.$toast("文章id和用户id匹配不成功!")
        //                 break;
        //           case 'success':
        //                 break;
        //           default:
        //           // 更新推荐栏
        //           if(res.data.bidding_price){
        //             this.curArtBidding_price = res.data.bidding_price
        //           }
        //           /* 同步首页*/
        //           this.noticeArrHandler(res.data.result)
        //           // var tempVar  = this.userInfo.user_gold - params.num
        //           //设置金币
        //           this.setHandler(['user_gold',res.data.user_gold])
        //           this.$toast("竞标成功!")
        //           // 关闭popup
        //           this.showPopupForBiddingModel = false
        //           // this.showPopupForBidding = false
        //           /* default结束*/
        //         }
        //     }
        // )
      },
    },
    /* 计算*/
    computed:{
      isBuffering:{
              get () {
                    return this.$store._modulesNamespaceMap['win/'].context.state.isBuffering
              },
              set (value) {
                this.$store._modulesNamespaceMap['win/'].context.commit('isBufferingHandler', value)
              }
            },
      isDoing:{
              get () {
                    return this.$store._modulesNamespaceMap['win/'].context.state.isDoing
              },
              set (value) {
                this.$store._modulesNamespaceMap['win/'].context.commit('isDoingHandler', value)
              }
            },
      ...gridVuex.mapState({
           	grid : state => state.grid
           }),
      // 用户是否登录
      ...login.mapState({
             hasLogin : state => state.hasLogin
           }),
      //用户信息
      ...login.mapState({
           	userInfo : state => state.userInfo
           }),
     // ...nav.mapState({
     //      	code : state => state.code
     //      })
     // curArtBidding_price:{
     // 	get () {
     // 		return this.$store._modulesNamespaceMap['notice/'].context.state.curArtBidding_price
     // 	  },
     // 	set (value) {
     // 	this.$store._modulesNamespaceMap['notice/'].context.commit('curArtBidding_priceHandler', value)
     // 	}
     // },
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      Notice,
      NoticePlate
    },
    /* 挂载阶段*/
    mounted() {
      this.isBuffering = true
      /* *********************************首页*/
      var params = {
        articleId : this.$route.query.articleId,
      }
      this.$api.article.getNowBidding(params).then(
          res =>{
            if(res.data.bidding_price){
              this.curArtBidding_price = res.data.bidding_price
            }
            this.noticeArrHandler(res.data.result)
          }
      )
      /* ********************************************板块*/
      let paramsPlate = {
        articleId : this.$route.query.articleId,
      }
      for(let i = 0; i<this.grid.length;i++){
        if(this.grid[i].text == this.$route.query.article_plate){
          paramsPlate.bidding_type = i
        }
      }
      
      this.$api.article.getNowBiddingPlate(paramsPlate).then(
          res =>{
            this.isBuffering = false
            if(res.data.bidding_price){
              this.curArtBiddingPlate_price = res.data.bidding_price
            }

            this.noticePlateArrHandler(res.data.result)
          }
      )
       /* ********************************************板块*/
    },
    // 监听
    watch:{
      // '$route.path': {
      //     handler(newVal, oldVal) {
      //       console.log(newVal)
      //       switch(newVal){
      //         case '/bidding':
      //             this.curArtBidding_price = 0
      //             break;
      //       }
      //     },
      //     // immediate: true,
      //     // deep: true
      // }
    },
    beforeRouteLeave( to,from,next ){
      this.curArtBidding_price = 0
      this.curArtBiddingPlate_price = 0
                next();  //写了这个，才会从当前导航离开，否则会一直停留在当前导航页
    }
  }
</script>

<style scoped lang="scss">
  .am-biddingBox{
    // margin-top: 46px;

  }
  .biddingBox{
    .curArtBidding_price{
      color:$red;
      font-size: 30px;
       @include flex(row,center,center)
       .fire{
         font-size:40px
       }
    }
    .bidding{
      background-color: $red;
      font-size:50px;
      color:white;
      @include wh(750px,100px)
      @include flex(row,center ,center)
      i{
        font-size:80px;

      }
    }
  }
  /* 竞标*/
  .showPopupForBiddingModel{
    background-color: $commentGbc;
    font-size: 36px;
    @include flex(column ,space-around,center)
    .tit{
      // margin-top: 25px;
      text-align: center;
    }
    .one{
      // font-size: 36px;
      // margin-top: 25px;
      @include flex(row,center,center)
      // 钻石
      .gold{
        color: $red;
        font-size: 36px;
        position: relative;
        top:3px;
        margin-right:2px;
      }
      .userGold{
        color: $red;
      }
    }
    input {
      // margin-top: 25px;
      border: 0;  // 去除未选中状态边框
      outline: none; // 去除选中状态边框
      //background-color: rgba(0, 0, 0, 0);// 透明背景
      @include wh(500px,60px);
    }
    .pay{
     // margin: 25px auto 0;
     background-color: $red;
     color: #FFFFFF;
     border-radius: 30px;
     @include flex(row,center,center)
     @include wh(500px,100px);
    }
  }
</style>



